import Link from "next/link";
import { useSpring, animated } from "react-spring";

import { Button } from "reactstrap";

import styled from "styled-components";

const ESWButtonDiv = styled.div`
  font-family: "Charm", cursive;

  .btn-red {
    background: #e53030;
    border: 3px solid #fff;
    border-radius: 100%;
    box-shadow: 0 -2px 0 3px #c91919 inset, 0 5px 5px rgba(65, 8, 8, 0.17),
      0 15px rgba(255, 255, 255, 0.25) inset;
    cursor: pointer;
    display: inline-block;
    height: 10vw;
    width: 10vw;
  }
  .btn-red:hover {
    background: #333;
    box-shadow: 0 -2px 0 3px #1a1a1a inset, 0 5px 5px rgba(0, 0, 0, 0.17),
      0 15px rgba(255, 255, 255, 0.32) inset;
  }

  #borderDiv {
  }

  .animatedESWButtonDiv {
  }
`;

const config = { mass: 5, tension: 2000, friction: 5000 };

function ESWButton() {
  const ESWButtonProps = useSpring({
    config,
    opacity: 1,
    from: {
      opacity: 0
    }
  });

  return (
    <ESWButtonDiv>
      <div id="ESWButtonDiv">
        <animated.div className="animatedESWButtonDiv" style={ESWButtonProps}>
          <Link href="/home">
            <a>
              <button className="btn-red btn-red:hover">
                Enter Santa's Workshop
              </button>
            </a>
          </Link>
        </animated.div>
      </div>
    </ESWButtonDiv>
  );
}

export default ESWButton;
